<script>
import Swiper from "swiper";
export default {
  created() {
    this.$store.state.bool = false;
  },
  // 生命周期钩子，最后销毁函数
  destroyed() {
    // 当组件被销毁时候，修改导航数据
    this.$store.state.bool = true;
    localStorage.advertpage = true;
  },
  mounted() {
    // 注意：.swiper-container swiper中实例化对象第一值，是swiper显示最外面对象容器。这个类在重复使用swiper容易冲突，重新定义类名解决
    new Swiper(".swiperGuidepage", {
      pagination: {
        el: ".wrapperGuidepage"
      }
    });
  }
};
</script>

